:root{
  --color1: #9885dd;
  --color2: #7584E0;/*#597bd6;*/
  --color3: #BAABCF;/*#542f99;*/
}

small {
  font-size: .2em;
}

#root {
  display: flex;
  justify-content: center;
}
.App {
  margin-left: 10vw;
  margin-right: 10vw;
  margin-top: 10vh;
  margin-bottom: 10vh;
}

@media (min-width: 1000px) {
  .App {
    width: 750px;
  }
}

.Header {
  height: 100px;
  overflow: visible;
}

nav {
  float: right;
  display: flex;
  justify-content: center;
}

.HeaderName {
  float: left;
  text-align: left;
  font-family: 'Major Mono Display', monospace;
  text-decoration: none;
  color: var(--color2);
  font-size: 2.6em;
  display: flex;
  justify-content: center;
}
.about-header {
  display: inline;
  margin: 10px;
}
.hello {
  float: left;
}

.imgcap {
  float: right;
}

header{
  font-weight:bold;
}
.current-tab a {
  color: var(--color2);
}
a {
  /*text-decoration: none;*/
  color:inherit;
  text-decoration: inherit;
  font-weight:bold;
}

a:hover{
  color:var(--color3);
}

@media (max-width: 660px) {
  .HeaderName {
    float: none;
    padding-bottom: 1vw;
  }
  nav {
    float: none;
    align-items: center;
  }
  nav li a {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (max-width: 350px) {
  .HeaderName {
    font-size: 10vw;
  }
}

nav ul {
  text-align: center; 
  padding-inline-start: 0px;
}
nav li {
  display: inline-block;
  font-family: 'Heebo', sans-serif;
  font-size: 1em;
  &:current {
    color: gray;
  }
}

nav li a {
  padding-left: 20px;
  padding-right: 20px;
  text-decoration: none;
  color: black;
}
.content {
  /*display: inline-block;*/
  font-family: 'Heebo',sans-serif;
  padding-top: 20px;
}

.about-img {
  width: 100%;
  height: auto;
  padding-bottom: 30px;
  display: inline-block;
  
}
.about-img img {
  width:100%;
  height: auto;
  -webkit-transition: 0.3s ease;
 transition: 0.3s ease;
}

.about-img img:hover {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

.about .media {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.about .caption {
  text-align: center;
}

.funnings {
  display: inline-block;
}

.icon {
  padding: 10px 20px 10px 20px;
  display: inline-block;
}

.icons {
  display: block;
  text-align: center;
}

.projects .media {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.media {
  text-align: center;
}

.media iframe {
  min-width: 100%;
  max-width: 100%;
  height: 40vh;
  min-height: 200px;
  padding-bottom: 30px;
}

.projects .media img {
  height: auto;
  width: 100%;
  padding-bottom: 30px; 
}

.project .media .phoneimgsize {
  width:50%;
  height:auto;
  padding-bottom: 30px;
}

.projects .description {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.projects .description .icon {
  align-self: center;
}

.project {
  padding-bottom: 100px;
}
